import Vue from 'vue';
import VueRouter from 'vue-router';
import home from '../views/home.vue';
import login from '../views/login.vue';
import detail from '../views/detail.vue';

Vue.use(VueRouter);

const router = new VueRouter({
	routes: [
		{
			path: '/',
			name: 'home',
			component: home
		},
		{
			path: '/login',
			name: 'login',
			component: login
		},
		{
			path: '/detail/:id',
			name: 'detail',
			component: detail
		}
	]
});


 export default router;



