
// api 路径
const HOST = 'https://cnodejs.org/api/v1';

const GET_TOPICS = '/topics';

const GET_TOPIC_BY_ID = '/topic/';

const GET_LOGIN = '/accesstoken';

const GET_COLLECT = '/topic_collect/collect';

const GET_DEL_COLLECT = '/topic_collect/de_collect';

const GET_UP = '/reply/';

export default {
	getTopics () {
		return HOST + GET_TOPICS;
	},
	getTopicById (id) {
		return HOST + GET_TOPIC_BY_ID + id;
	},
	getLogin () {
		return HOST + GET_LOGIN;
	},
	getCollect () {
		return HOST + GET_COLLECT;
	},
	getDelCollect () {
		return HOST + GET_DEL_COLLECT;
	},
	getUp (id) {
		return HOST + GET_UP + id + '/ups';
	}
}


